export function smoothScrollToBottom() {
  // -----------------------
  //    div  
  //     cardHeight
  const { height: cardHeight } = document
    .querySelector('.gallery')
    .firstElementChild.getBoundingClientRect();
  console.log(cardHeight * 2);
  // // ------------------------------
  // //     
  //         
  window.scrollBy({
    top: cardHeight * 2,
    behavior: 'smooth',
  });
}
